/* Imports ----------------------------------------------------------------- */

// Import D3 and the fetch polyfill
import "whatwg-fetch";
import * as d3 from "d3";

/* Color lookup ------------------------------------------------------------ */

function getColor(language) {
  const scale = d3.schemeTableau10;
  let color = null;
  switch (language) {
    case "en":
      color = scale[0];
      break;
    case "vi":
      color = scale[1];
      break;
    case "zh-cn":
      color = scale[2];
      break;
    case "ar":
      color = scale[3];
      break;
    case "ko":
      color = scale[4];
      break;
    case "th":
      color = scale[5];
      break;
    case "ru":
      color = scale[6];
      break;
    case "es":
      color = scale[7];
      break;
    case "unknown":
      color = scale[8];
      break;
    default:
      color = scale[9];
  }
  return color;
}

/* ForceGraph -------------------------------------------------------------- */

function forceGraph(data) {
  
  const nodes = data.nodes;
  const links = data.links;

  const width = 900;
  const height = 900;

  const svg = d3.select("#vis")
    .append("svg")
    .attr("width", width)
    .attr("height", height)

  const simulation = d3.forceSimulation(nodes)
    .force("charge", d3.forceManyBody().strength(-3.4))
    .force("center", d3.forceCenter(width / 2, height / 2))
    .force("collision", d3.forceCollide().radius(d => Math.log(d.total_notices) + 3))
    .force("link", d3.forceLink(links).id((d) => d.id).distance(30))
    .on("tick", ticked);

  function updateNodes() {
    const update = d3.select("svg")
      .selectAll("circle")
      .data(nodes)
      .join("circle")
      .attr("cx", d => d.x)
      .attr("cy", d => d.y)
      .attr("r", d => Math.log(d.total_notices) + 2)
      .attr("fill", d => getColor(d.language))
      .on("mouseover", (e, d) => console.log(d));
  }

  function updateLinks() {
    const update = d3.select("svg")
      .selectAll("line")
      .data(links)
      .join("line")
      .attr("x1", function(d) {
        return d.source.x
      })
      .attr("y1", function(d) {
        return d.source.y
      })
      .attr("x2", function(d) {
        return d.target.x
      })
      .attr("y2", function(d) {
        return d.target.y
      })
      .attr("stroke", "#c0c0c0")
      .attr("stroke-opacity", 0.3)
      .attr("stroke-width", d => Math.log(d.value) + 1);
  }

  function ticked () {
    updateLinks();
    updateNodes();
  }
}
/* Main -------------------------------------------------------------------- */

window.fetch("undirected_hrefs_network.json")
    .then(function(response) {
        return response.json();
    }).then(function(data) {
      forceGraph(data);
    }).catch(function(error) {
        console.log(error);
    });